import React from 'react';
import Airlines from './mockAirlines'; 
import { Form, Button } from 'react-bootstrap';
import Swal from 'sweetalert2'
import { withRouter } from 'react-router-dom';

class TicketIssue extends React.Component{
  constructor(props){
    super(props); 
    this.state = {
      firstName: '',
      lastName: '',
      emailid: '',
      phoneNumber: '',
      departure: '',
      arrival: '',
      date: '',
      airlines: '',
      seat: '',
      age: '',
      price: '',
      airlinesList: [],
      available: '',
      Quantity: '',
      time: '',
    }
  }

  componentDidMount() {
    console.log(this.props.location.state);
    let price = this.checkPrice();
    let time = this.checkTime();
    console.log(price);
    this.setState({
      firstName: this.props.location.state.firstName,
      lastName: this.props.location.state.lastName,
      emailid: this.props.location.state.emailid,
      phoneNumber: this.props.location.state.phoneNumber,
      departure: this.props.location.state.departure,
      arrival: this.props.location.state.arrival,
      date: this.props.location.state.date,
      airlines: this.props.location.state.airlines,
      seat: this.props.location.state.seat,
      age : this.props.location.state.age,
      airlinesList: Airlines,
      available: this.props.location.state.available,
      price: price,
      time: time,
    });
  }

  checkPrice = () => {
    let airlines = this.props.location.state.airlines;
    let airlinesList = Airlines;
    let price;
    airlinesList.forEach((item, index) => {
      if(item.Name === airlines){
        price = item.price;
      }
    });
    return price;
  }

  checkTime = () => {
    let airlines = this.props.location.state.airlines;
    let airlinesList = Airlines;
    let time;
    airlinesList.forEach((item, index) => {
      if(item.Name === airlines){
        time = item.time;
      }
    });
    return time;
  }

  handleChange = (event) => {
    let nam = event.target.name;
    let val = event.target.value;
    if(nam === "Quantity"){
      this.setState({ Quantity : val});
    }
  }


  bookTicket = (event) => {
    Swal.fire({
      title: 'Success',
      text: 'Your ticket is booked',
      icon: 'success',
      confirmButtonText: 'OK'
    });
    let obj = {firstName: this.state.firstName, lastName: this.state.lastName, emailid: this.state.emailid, phoneNumber: this.state.phoneNumber, departure: this.state.departure, arrival: this.state.arrival, age: this.state.age, seat: this.state.seat, date: this.state.date, time: this.state.time, airlines: this.state.airlines};
    this.props.history.push({ pathname: '/dashboard/viewTickets' , state: this.state});
  }

  render(){
    return(
      <div style={{marginTop: "50px"}}>
        <h2 style={{color: "dodgerblue"}}>The price per ticket for {this.state.seat} seat at {this.state.airlines} is {this.state.price}</h2>
        <Form style={{ width: "700px", marginLeft: "600px", marginTop: "50px" }}>
          <Form.Row style={{ marginLeft: "5px", color: "brown", fontWeight: "bold", fontSize: "20px" }}>
            <Form.Label>Enter Number of Tickets</Form.Label>
            <Form.Control type="number" placeholder="Enter Quantity" name="Quantity" onChange={this.handleChange}/>
          </Form.Row>
          <Button style={{ marginTop: "40px", marginLeft: "2px" }} variant="outline-success" onClick={this.bookTicket}>Pay</Button>
        </Form>
      </div>
    );
  }
}

export default TicketIssue = withRouter(TicketIssue);