import React from 'react';
import Airlines from './mockAirlines';
import { withRouter } from 'react-router-dom';
import {Button} from 'react-bootstrap';

class ValidateTickets extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      firstName: '',
      lastName: '',
      emailid: '',
      phoneNumber: '',
      departure: '',
      arrival: '',
      date: '',
      airlines: '',
      seat: '',
      age: '',
      airlinesList: [],
      available: ''
    }
  }

  componentDidMount() {
    console.log(this.props.location.state);
    this.setState({
      firstName: this.props.location.state.firstName,
      lastName: this.props.location.state.lastName,
      emailid: this.props.location.state.emailid,
      phoneNumber: this.props.location.state.phoneNumber,
      departure: this.props.location.state.departure,
      arrival: this.props.location.state.arrival,
      date: this.props.location.state.date,
      airlines: this.props.location.state.airlines,
      seat: this.props.location.state.seat,
      age : this.props.location.state.age,
      airlinesList: Airlines,
      available: '',
    });
  }

  checkAvailability = () => {
    let seat = this.state.seat;
    let airlines = this.state.airlines;
    let List = this.state.airlinesList;
    let isAvailable = false;

    console.log(airlines);

    if (seat === "Aisle") {
      console.log("aisle")
      List.forEach((item, index) => {
        if (item.Name === airlines && item.aisle === true) {
          console.log("in aisle");
          return isAvailable = true;
        }
      });
    }
    else if (seat === "Window") {
      List.forEach((item, index) => {
        if (item.Name === airlines && item.window === true) {
          return isAvailable = true;
        }
      });
    }
    else if (seat === "Front") {
      List.forEach((item, index) => {
        if (item.Name === airlines && item.front === true) {
          return isAvailable = true;
        }
      });
    }
    else if (seat === "Back") {
      List.forEach((item, index) => {
        if (item.Name === airlines && item.back === true) {
          return isAvailable = true;
        }
      });
    }
    console.log(isAvailable);
    this.setState({ available: isAvailable})
  }

  bookAgain = (event) => {
    this.props.history.push('/dashboard/book');
  }

  passTicket = (event) => {
    this.props.history.push({pathname: '/dashboard/issueTickets', state: this.state})
  }

  render() {
    let display = '';
    if (this.state.available === true) {
      display = <div>
                  <h2 style={{ color: 'brown' }}>Your ticket from {this.state.departure} to {this.state.arrival} is available on {this.state.airlines} on {this.state.date} as you requested</h2>
                  <h2 style={{ color: 'dodgerblue'}}> Your seat preference of {this.state.seat} is also available</h2>
                  <h2 style={{ color: 'dodgerblue'}}> Confirm to book your tickets</h2>
                  <Button style={{ marginTop: "40px", marginLeft: "2px" }} variant="outline-success" onClick={this.passTicket}>Confirm and Pay</Button>
                </div>
    }
    else if(this.state.available === false){
      display = <div>
                  <h2 style={{ color: 'brown' }}>Your ticket from {this.state.departure} to {this.state.arrival} is available on {this.state.airlines} on {this.state.date} as you requested</h2>
                  <h2 style={{ color: 'red'}}> But we regret to inform you that your seat preference of {this.state.seat} is not available.</h2>
                  <h2 style={{ color: 'dodgerblue'}}>Please select other options </h2>
                  <Button style={{ marginTop: "40px", marginLeft: "2px" }} variant="outline-danger" onClick={this.bookAgain}>Book Again</Button>
                </div>
    }
    return (
      <div>
        <Button style={{ marginTop: "40px", marginLeft: "2px", marginBottom: "20px" }} variant="outline-success" onClick={this.checkAvailability}>Check the Availability</Button>
        {display}
      </div>
    );
  }
}

export default ValidateTickets = withRouter(ValidateTickets);