import React from 'react';
import { Table } from 'react-bootstrap';
import { withRouter } from 'react-router-dom';
import Airlines from './mockAirlines';

class ViewTickets extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      ticket : [],
    }
  }

  static ticket = [];

  componentDidMount() {
    console.log(this.props.location.state);
    let firstName = this.props.location.state.firstName;
    let lastName = this.props.location.state.lastName;
    let emailid = this.props.location.state.emailid;
    let phoneNumber = this.props.location.state.phoneNumber;
    let departure = this.props.location.state.departure;
    let arrival = this.props.location.state.arrival;
    let date = this.props.location.state.date;
    let airlines = this.props.location.state.airlines;
    let seat = this.props.location.state.seat;
    let age  = this.props.location.state.age;
    //let airlinesList = Airlines;
    //let available = this.props.location.state.available;
  //  let price = this.props.location.state.price;
    let time = this.props.location.state.time;
    let Quantity = this.props.location.state.Quantity;
    let obj = {firstName: firstName, lastName: lastName, emailid: emailid, phoneNumber: phoneNumber, departure: departure, arrival: arrival, age: age, seat: seat, date: date, time: time, airlines: airlines, Quantity: Quantity};
    ViewTickets.ticket = this.state.ticket;
    ViewTickets.ticket.push(obj);
    console.log(ViewTickets.ticket);
    this.setState({
      ticket: ViewTickets.ticket
    });
  }

  render() {
    return (
      <div>
        <Table striped bordered hover>
          <thead>
            <tr>
              <th>S.No</th>
              <th>First Name</th>
              <th>Last Name</th>
              <th>Email ID</th>
              <th>Phone Number</th>
              <th>Departure Airport</th>
              <th>Arrival Airport</th>
              <th>Date of Journey</th>
              <th>Time of Jouney</th>
              <th>Seat Preference</th>
              <th>Airlines</th>
              <th>Quantity</th>
            </tr>
          </thead>
          <tbody>
            {this.state.ticket.map((ticket, index) => (
              <tr key={index}>
                <td>{index + 1}</td>
                <td>{ticket.firstName}</td>
                <td>{ticket.lastName}</td>
                <td>{ticket.emailid}</td>
                <td>{ticket.phoneNumber}</td>
                <td>{ticket.departure}</td>
                <td>{ticket.arrival}</td>
                <td>{ticket.date}</td>
                <td>{ticket.time}</td>
                <td>{ticket.seat}</td>
                <td>{ticket.airlines}</td>
                <td>{ticket.Quantity}</td>
              </tr>
            ))}
          </tbody>
        </Table>
      </div>
    );
  }
}

export default ViewTickets = withRouter(ViewTickets);