import React from 'react';
import './App.css';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
import { Redirect } from 'react-router-dom';

class Home extends React.Component {
  render() {
    return (
      <div>
        <h1 style={{margin: "30px",marginLeft: "50px", color:"brown", marginTop: "50px"}}>Welcome to Unifly'98 </h1>
        <h4 style={{margin: "30px",marginLeft: "80px", marginBottom: "50px"}}>Book your flight tickets at the best price at your convenience</h4>
        <hr style={{borderWidth: "5px"}}></hr>
        <Redirect to="/login" />
      </div>
    );
  }
}

export default Home;
