import React from 'react';
import {Navbar, Nav} from 'react-bootstrap';
import { withRouter, Redirect, Link } from 'react-router-dom';

class Dashboard extends React.Component {
  render() {
    return (
      <div>
        <Navbar bg="primary" variant="dark">
          <Navbar.Brand as={Link} to="/dashboard">Unifly'98</Navbar.Brand>
          <Nav className="mr-auto">
            <Nav.Link as={Link} to="/dashboard/book">Book a ticket</Nav.Link>
            <Nav.Link as={Link} to="/dashboard/viewTickets">View your tickets</Nav.Link>
          </Nav>
          <Nav>
            <Nav.Link href="/login">Logout</Nav.Link>
          </Nav>
        </Navbar>
        <Redirect to="/dashboard/book" />
      </div>
    );
  }
}

export default Dashboard = withRouter(Dashboard);