import React from 'react';
import { Form, Col , Button} from 'react-bootstrap';
import { withRouter } from 'react-router-dom';

class BookTicket extends React.Component {

  constructor(props){
    super(props);
    this.state = {
      firstName: '',
      lastName: '',
      emailid: '',
      phoneNumber: '',
      arrival: '',
      departure: '',
      date: '',
      airlines: '',
      seat: '',
      age: '',
    }
  }

  handleChange = (event) => {
    let nam = event.target.name;
    let val = event.target.value;
    if(nam === "firstName"){
      this.setState({ firstName : val});
    }
    if(nam === "lastName"){
      this.setState({ lastName : val});
    }
    if(nam === "emailid"){
      this.setState({ emailid : val});
    }
    if(nam === "phoneNumber"){
      this.setState({ phoneNumber : val});
    }
    if(nam === "departure"){
      this.setState({ departure : val});
    }
    if(nam === "arrival"){
      this.setState({ arrival : val});
    }
    if(nam === "date"){
      this.setState({ date : val});
    }
    if(nam === "airlines"){
      this.setState({ airlines : val});
    }
    if(nam === "seat"){
      this.setState({ seat : val});
    }
    if(nam === "age"){
      this.setState({ age : val});
    }
  }
  
  passTicket = (event) => {
    console.log(this.state);
    this.props.history.push({pathname: '/dashboard/validate', state: this.state})
  }

  render() {
    return (
      <div style={{ marginTop: "50px" }}>
        <h2 style={{ color: "dodgerblue", fontStyle: "bold" }}>Book a Ticket</h2>
        <Form style={{ width: "700px", marginLeft: "600px", marginTop: "50px" }}>
          <Form.Row style={{ marginLeft: "5px", color: "brown", fontWeight: "bold", fontSize: "20px" }}>
            <Form.Label>Enter your Name  </Form.Label>
          </Form.Row>
          <Form.Row>
            <Col>
              <Form.Control placeholder="First name" name="firstName" onChange={this.handleChange} />
            </Col>
            <Col>
              <Form.Control placeholder="Last name" name="lastName" onChange={this.handleChange}/>
            </Col>
          </Form.Row>
          <Form.Row style={{ marginTop: "40px", marginLeft: "2px" }}>
            <Form.Label style={{ marginLeft: "5px", color: "brown", fontWeight: "bold", fontSize: "20px" }}  >Enter Email address</Form.Label>
            <Form.Control type="email" placeholder="Enter email" name="emailid" onChange={this.handleChange} />
          </Form.Row>
          <Form.Row style={{ marginTop: "40px", marginLeft: "2px" }}>
            <Form.Label style={{ marginLeft: "5px", color: "brown", fontWeight: "bold", fontSize: "20px" }}  >Enter Phone Number </Form.Label>
            <Form.Control type="number" placeholder="Enter Phone Number with +91" name="phoneNumber" onChange={this.handleChange}/>
          </Form.Row>
          <Form.Row style={{ marginTop: "40px", marginLeft: "2px" }}>
            <Form.Label style={{ marginLeft: "5px", color: "brown", fontWeight: "bold", fontSize: "20px" }}  >Enter Age</Form.Label>
            <Form.Control type="number" placeholder="Enter Age" name="age" onChange={this.handleChange}/>
          </Form.Row>
          <Form.Row style={{ marginTop: "40px", marginLeft: "2px" }}>
            <Form.Label style={{ marginLeft: "5px", color: "brown", fontWeight: "bold", fontSize: "20px"  }}  >Enter Departure Airport </Form.Label>
            <Form.Control type="text" placeholder="Enter Departure" name="departure" onChange={this.handleChange}/>
          </Form.Row>
          <Form.Row style={{ marginTop: "40px", marginLeft: "2px" }}>
            <Form.Label style={{ marginLeft: "5px", color: "brown", fontWeight: "bold", fontSize: "20px" }}  >Enter Arrival Airport </Form.Label>
            <Form.Control type="text" placeholder="Enter Arrival" name="arrival" onChange={this.handleChange} />
          </Form.Row>
          <Form.Row style={{ marginTop: "40px", marginLeft: "2px" }}>
            <Form.Label style={{ marginLeft: "5px", color: "brown", fontWeight: "bold", fontSize: "20px" }}  >Select Journey Date </Form.Label>
            <Form.Control type="date" name="date" onChange={this.handleChange}/>
          </Form.Row>
          <Form.Row style={{ marginTop: "40px", marginLeft: "2px" }}>
            <Form.Label style={{ marginLeft: "5px", color: "brown", fontWeight: "bold", fontSize: "20px" }}  >Select Airlines </Form.Label>
            <Form.Control as="select" name="airlines" onChange={this.handleChange}>
              <option defaultValue="choose" >choose...</option>
              <option value="Air Vistara">Air Vistara</option>
              <option value="Indigo">Indigo</option>
              <option value="Air Asia">Air Asia</option>
              <option value="Air India">Air India</option>
              <option value="SpiceJet">SpiceJet</option>
            </Form.Control>
          </Form.Row>
          <Form.Row style={{ marginTop: "40px", marginLeft: "2px" }}>
            <Form.Label style={{ marginLeft: "5px", color: "brown", fontWeight: "bold", fontSize: "20px" }}  >Select Seat Preferences </Form.Label>
            <Form.Control as="select" name="seat" onChange={this.handleChange}>
            <option defaultValue="choose" >choose...</option>
              <option value="Aisle">Aisle</option>
              <option value="Window">Window</option>
              <option value="Front">Front</option>
              <option value="Back">Back</option>
            </Form.Control>
          </Form.Row>
          <Button style={{ marginTop: "40px", marginLeft: "2px" }} variant="outline-primary" onClick={this.passTicket}>Check Availability</Button>
        </Form>
      </div>
    );
  }
}

export default BookTicket = withRouter(BookTicket);